import _ from "lodash"

export default {
  data() {
    return {
      notifs: [],
      text: {
        error: 'Error',
        info: 'Info',
        success: 'Success',
        warning: 'Warning',
      }
    }
  },

  destroyed() {
    this.clear()
  },

  methods: {
    clear() {
      this.notifs.forEach(notif => {
        notif.close()
      })

      this.notifs = []
    },

    handleError(e) {
      let type = 'error'
      let { response } = e

      if (response) {
        let { data } = response

        if (   data
            && data.message) {
          if (_.isArray(data.message)) {
            this.handleMessages(data.message, type)
          } else if (_.isString(data.message)) {
            this.handleMessage({
              message: data.message,
              type
            })
          }
        } else {
          this.handleMessage({
            message: e.message,
            type
          })
        }
      } else {
        let { request } = e

        if (   request
            && request.status == 0) {
          return
        }

        this.handleMessage({
          message: e.message,
          type
        })
      }
    },

    async handleMessage({ message, type }) {
      return new Promise((resolve) => {
        setTimeout(() => {
          let notif = this.$notify({
            title: this.text[type],
            duration: 0,
            message,
            type
          })

          // Store a reference to the notif instance
          this.notifs.push(notif)
          resolve()
        }, 100)
      })
    },

    async handleMessages(messages, type) {
      let index = 0

      while (messages[index]) {
        let message = messages[index]
        await this.handleMessage({ message, type })
        index++
      }
    },
  }
}


